* {
   box-sizing: border-box;
   margin: 0;
   padding: 0;
}

body {
    font-family: Arial, Arial, Helvetica, sans-serif;
    line-height: 1.4;
}

a {
    color: #333;
    text-decoration: none;
}

.container {
    padding: 0 1rem;
}

.btn {
    display: inline-block;
    border: none;
    background: #197294;
    color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 15px;
}

.btnclear {
    display: inline-block;
    border: none;
    background: #a55a18;
    color: #fff;
    padding: 10px 20px;
    cursor: pointer;
    font-size: 15px;
    margin: 8px;
    font-weight: bold;

}

.btn-hover {
    background: #666
}

.save {
  padding: 10px 10px;
  font-size: 10px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  background-color: rgb(51, 105, 51);
  border: '1px';
  border-radius: 15px;
  font-weight: bold;
}
.save:hover {background-color: #377739}

.save:active {
  background-color: #3e8e41;
  transform: translateY(4px);
}

.cancel {
  padding: 10px 10px;
  font-size: 10px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color:rgb(19, 19, 19);
  background-color: '#fff';
  border: '2px';
  border-radius: 15px;
  font-weight: bold;
}

.cancel:hover {background-color: rgb(224, 224, 224)}

.cancel:active {
  background-color: rgb(138, 128, 128);
  transform: translateY(4px);
}

.delete {
  padding: 8px 8px;
  font-size: 10px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  background-color: rgb(214, 84, 84);
  border: '1px';
  border-radius: 15px;
  font-weight: bold;
    float: right;

}
.delete:hover {background-color: rgb(236, 17, 17)}
.delete:active {
  background-color: rgb(236, 17, 17);
  transform: translateY(4px);
}
.edit {
  padding: 8px 8px;
  font-size: 10px;
  text-align: center;
  cursor: pointer;
  outline: none;
  color: #fff;
  background-color: rgb(115, 123, 235);
  border: '1px';
  border-radius: 15px;
  font-weight: bold;
  float: right;
}
.edit:hover {background-color:rgb(30, 39, 161)}
.edit:active {
  background-color: rgb(30, 39, 161);
  transform: translateY(4px);
}